.film-table {
  font-family: Arial, Helvetica, sans-serif;
  border-collapse: collapse;
  width: 100%;
}

.film-table td, .film-table th {
  border: 1px solid #ddd;
  padding: 8px;
  text-align: center;
}

.film-table tr:nth-child(even){background-color: #f2f2f2;}

.film-table tr:hover {background-color: #ddd;}

.film-table th {
  padding-top: 12px;
  padding-bottom: 12px;
  text-align: left;
  background-color: #ffc0cb;
  color: white;
}